const ProductEditPage = () => {
  const [product, setProduct] = useState({
    name: "",
    sku: "",
    price: "",
    category: "",
    status: "Active",
    mainImage: "https://cdn.dribbble.com/userupload/4577926/file/original-e38b605f1e88ccc0f6fff316c7df5699.png?resize=1504x1128&vertical=center", // Default image URL
    sideImages: [],
    color: "",
    size: "",
    description: "",
    material: "",
    salePrice: "",
    purchasePrice: "",
    discountPercentage: "",
    inventoryStock: "",
  });

  const handleMainImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const imageUrl = URL.createObjectURL(file);
      setProduct({ ...product, mainImage: imageUrl });
    }
  };

  const handleSideImagesChange = (e) => {
    const files = Array.from(e.target.files);
    const imageUrls = files.map((file) => URL.createObjectURL(file));
    setProduct({ ...product, sideImages: imageUrls });
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h2 className={`${styles.pageTitle} text-center mb-4`}>
          {isEditing ? "Edit Product" : "Add Product"}
        </h2>

        <Card className={`${styles.cardContainer} shadow-sm mb-4`}>
          <Card.Body>
            <Row>
              <Col md={6}>
                <div className={`${styles.innerCard} p-4`}>
                  <h3>Basic Details</h3>
                  <Form onSubmit={handleFormSubmit}>
                    {/* Other form fields */}
                    <Form.Group controlId="mainImage" className="mb-3">
                      <Form.Label>Upload Main Image</Form.Label>
                      <Form.Control
                        type="file"
                        accept="image/*"
                        onChange={handleMainImageChange}
                      />
                    </Form.Group>

                    {product.mainImage && (
                      <div>
                        <img
                          src={product.mainImage}
                          alt="Product Preview"
                          style={{
                            width: "150px",
                            height: "auto",
                            marginTop: "10px",
                          }}
                        />
                      </div>
                    )}
                  </Form>
                </div>
              </Col>
            </Row>
          </Card.Body>
        </Card>
      </div>
    </Layout>
  );
};
